.container-display{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    width: 95%;
    margin: 0 auto;
}

.container-display .box{
   display: flex;
   flex-direction: column;
   width: 19%;
   margin-right: 2rem;
}

.box-header p{
    display: flex;
    align-items: center;
    margin-left: 0.5rem;
}

@media screen and (max-width: 1050px) {
    .container-display{
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        width: 80%;
        margin: 0 auto;
    }
    .container-display .box{
        display: flex;
        /* flex-direction: row; */
        width: 100%;
        margin-right: 2rem;
     }
 }