.UserDiv{
    display: flex;
    flex-direction: row;
    width: 90%;
    margin: 0 auto;
}

.User{
    width: 20%;
    margin-right:1rem
}

.User p{
    margin: 1rem;
}

@media screen and (max-width: 1050px) {
    .container-display{
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        width: 80%;
        margin: 0 auto;
    }
    .container-display .box{
        display: flex;
        /* flex-direction: row; */
        width: 100%;
        margin-right: 2rem;
     }
     .UserDiv{
        display: flex;
        flex-direction: column;
        width: 90%;
        margin: 0 auto;
    }
    
    .User{
        width: 80%;
        margin-right:1rem;
    }

    .User p{
        text-align: center;
    }

 }

 