.display-menu{
  display: flex;
  flex-direction: row;
  width: 95%;
  margin: 0 auto;
}
.menu1,.menu2{
  margin: 1rem;
}

.display-menu select{
  margin-left: 0.6rem;
  border: none;
  font-size: 1rem;
  padding: 0.5rem;
}

.display-menu option{
  font-size: 1rem;
  margin: 0.5rem;
}
.display-menu option:hover{
  background-color: rgb(151, 143, 150);
}

.display-menu select:hover{
  box-shadow: 0 0 0.5rem rgb(143, 146, 151);
}


@media screen and (max-width: 1050px) {
  .display-menu{
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  .display-menu option{
    font-size: 0.6rem;
    margin: 0.5rem;
  }

}

@media screen and (max-width: 430px) {
  .menu1,.menu2{
    margin: 1rem;
    display: flex;
    flex-direction: column;
  }

  .display-menu span{
    padding-bottom: 0.6rem;
  }

  .display-menu select{
    margin-left: 0rem;
    border: none;
    font-size: 1rem;
    padding: 0.5rem;
  }
    
}