.card{
    padding: 0.5rem 0.5rem;
    background-color: white;
    margin:0 auto;
    border-radius: 0.5rem;
    box-shadow: 0 0 0.8rem rgb(143, 146, 151);
    width: 90%;
    margin-bottom: 1rem;
}
.card .container{
    width: 95%;
    margin: 0 auto;
}

.container h2{
    color: rgb(143, 146, 151);
    font-weight: 600;
    font-size: 0.8rem;
  }
.container h1{
    color: rgb(40,42,48);
    font-weight: 600;
    font-size: 0.8rem;
}

.container .text{
    display: flex;
    flex-direction: row;
    /* margin-right: 1rem; */
    padding: 0rem 1rem;
    border: 0.1rem solid rgb(242, 244, 246);
    border-radius: 1rem;
}

.container .text .icon{
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 0.8rem;
}

.container .text h2{
    color: rgb(143, 146, 151);
    font-weight: 600;
    margin-left: 1rem;
    font-size: 0.7rem;
  }

  .box1 h2{
    display: flex;
    justify-content: space-between;
  }

  .box1 h2 .profileIcon{
    font-size: 1rem;
  }

  /* //// mediaqueries */
  @media screen and (max-width: 1050px) {
    .container .text{
        display: flex;
        flex-direction: row;
        /* margin-right: 1rem; */
        padding: 0rem 0.8rem;
        border: 0.1rem solid rgb(242, 244, 246);
        border-radius: 1rem;
 }
}